<script>
  import { Swipe, SwipeItem } from "svelte-swipe";
  import { pop } from "svelte-spa-router";
  import { onMount, tick } from "svelte";
  import Zoom from "svelte-zoom";
  import { slide } from "svelte/transition";

  export let index = 0;
  export let images = [];
</script>

<main>
  <div class="fixed-top p-3 d-flex justify-content-between align-items-center">
    <div class="p-1">
      <button class="btn btn-floating btn-lg btn-dark" on:click={() => pop()}
        ><i class="fas fa-chevron-left" /></button
      >
    </div>
    <div class="p-1">
      <div class="d-flex justify-content-center p-1">
        {#each images as image, i}
          <div
            class="img d-flex align-items-center justify-content-center m-1"
            class:img-selected={index == i}
          >
            <img
              src={image}
              class="mh-100 mw-100"
              alt=""
              on:click={() => {
                index = i;
              }}
            />
          </div>
        {/each}
      </div>
    </div>
    <div class="p-1">
      <h5>
        <span class="badge bg-dark text-light">
          {Number(index) + 1}/{images.length}
        </span>
      </h5>
    </div>
  </div>
  <div class="swipe h-100 py-5">
    <div class="w-100 h-100">
      <div class="d-flex justify-content-center align-items-center p-5 h-100">
        {#each images as image, i}
          <img
            src={image}
            alt=""
            class="m-auto h-auto mw-100 mh-100 py-5"
            class:d-none={index != i}
          />
        {/each}
      </div>
    </div>
  </div>
  <div class="w-100 d-flex justify-content-between fixed-bottom p-3">
    <div>
      <button
        class:d-none={index == 0}
        class="btn btn-dark btn-lg"
        on:click={() => {
          if (index > 0) index--;
        }}><i class="fas fa-arrow-left" /> 이전 이미지</button
      >
    </div>
    <div>
      <button
        class:d-none={index == images.length - 1}
        class="btn btn-dark btn-lg"
        on:click={() => {
          if (index < images.length - 1) index++;
        }}>다음 이미지 <i class="fas fa-arrow-right" /></button
      >
    </div>
  </div>
</main>

<style>
  .img {
    height: 40px;
    width: 40px;
    background: #333333;
    border-radius: 10px;
  }

  .img-selected {
    background: #dddddd;
  }
  main {
    height: 100%;
    width: 100%;
    margin: 0;
    background: #000000 no-repeat center center fixed;
  }

  :global(.swipeable-item-inner) {
    height: 100%;
    max-height: 100%;
  }
</style>
